"use client"
import { useEffect, useState } from "react"
import { useAuth } from "@/lib/auth-context"
import { doc, getDoc } from "firebase/firestore"
import { db } from "@/lib/firebaseClient"
import { DashboardCentrosContent } from "@/components/dashboard/dashboard-centros-content"

export default function DashboardCentrosPage() {
  const { user, loading: authLoading } = useAuth()
  const [centerData, setCenterData] = useState<any | null>(null)
  const [adminData, setAdminData] = useState<any | null>(null)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const fetchData = async () => {
      if (!user) return
      const centerRef = doc(db, "padel_centers", user.uid)
      const centerSnap = await getDoc(centerRef)
      if (centerSnap.exists()) {
        setCenterData(centerSnap.data())
      }
      // Optionally fetch admin data if needed
      setAdminData({
        first_name: user.displayName || "",
        last_name: "",
        email: user.email || "",
      })
      setLoading(false)
    }
    if (user && !authLoading) fetchData()
  }, [user, authLoading])

  if (authLoading || loading) {
    return (
      <div className="min-h-screen bg-gradient-to-b from-slate-50 via-white to-slate-50 flex items-center justify-center">
        <div className="text-lg text-slate-600">Loading dashboard...</div>
      </div>
    )
  }
  if (!user || !centerData) {
    return (
      <div className="min-h-screen bg-gradient-to-b from-slate-50 via-white to-slate-50 flex items-center justify-center">
        <div className="text-lg text-red-600">Unable to load center data.</div>
      </div>
    )
  }
  return <DashboardCentrosContent admin={adminData} center={centerData} />
}
